var React = require('react');
var ReactBootstrap = require('react-bootstrap');
var Navigation = require('react-router').Navigation;

var Nav = ReactBootstrap.Nav;
var Navbar = ReactBootstrap.Navbar;
var NavItem = ReactBootstrap.NavItem;

window.MainNavBar = React.createClass({
  mixins: [Navigation],

  render: function () {
    return (
      <Navbar inverse>
        <Nav right>
          <NavItem href='#' onClick={function (e) {this._clickTransition(e, 'home')}.bind(this)}>Home</NavItem>
          <NavItem href='#' onClick={function (e) {this._clickTransition(e, 'videos')}.bind(this)}>Videos</NavItem>
          <NavItem href='#' onClick={function (e) {this._clickTransition(e, 'music')}.bind(this)}>Music</NavItem>
          <NavItem href='Resume.pdf'>Resume</NavItem>
        </Nav>
      </Navbar>
    );
  },

  _clickTransition: function (e, name) {
    this.transitionTo(name);
    e.preventDefault();
  }
});
